@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
.product-list {
  max-width: 1200px;
  width: 100%;
  margin: 10px auto;
  background: #679b00;

  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.product-list__item {
  width: 320px;
  margin: 10px 0;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.product-list__name {
  margin: 10px 0;
}

.product-list__img {
  max-width: 200px;
  max-height: 200px;
  transition: transform 1s;
  margin: 20px 0;
}

.product-list__img:hover {
  transform: scale(1.05);
}

.product-list__price-box {
  width: 100%;
  margin: 30px 0 10px;

  display: flex;
  align-items: center;
  justify-content: center;
}

.product-list__text {
  color: #ac0056;
  font-size: 1.2rem;
  font-weight: bold;
  margin: 3px 0;
}

.product-list__button {
  background: transparent;
  border: none;
  margin: 0 10px;
}

.product-list__button-img {
  max-width: 25px;
  width: 100%;
  cursor: pointer;
  opacity: 0.9;
  transition: transform 1s, opacity 0.5s;
}

.product-list__button-img:hover {
  transform: scale(1.1);
  opacity: 1;
}

.basket-menu {
  overflow: hidden;
  overflow-y: auto;
  height: 100%;
}

.basket-menu__title {
  text-align: center;
  font-size: 2rem;
  color: white;
}

.basket-menu__products {
  padding: 10px;
  margin: 0 0 40px;

  display: grid;
  grid-template-columns: repeat(auto-fill, 150px);
  grid-gap: 10px;
  justify-content: center;
}

.basket-menu__product-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  transition: transform 1s;
}

.basket-menu__product-item:hover {
  transform: scale(1.1);
}

.basket-menu__text-content {
  margin: 10px 0;
  height: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}

.basket-menu__product-title,
.basket-menu__product-text {
  display: inline;
  color: #9fee00;
  font-size: 1.2rem;
}

.basket-menu__product-title {
  margin: 5px 0 10px;
}

.basket-menu__product-img {
  max-width: 50%;
  max-height: 50%;
}

.basket-menu__services {
  width: 100%;
  height: 30px;
  background: #006363;

  position: absolute;
  bottom: -1px;

  display: flex;
  align-items: center;
  justify-content: center;
}

.basket-menu__text {
  font-size: 1.5rem;
  color: white;
}

.basket {
  max-width: 1200px;
  width: 100%;
  height: 50px;

  margin: 10px auto;
  background: #ad66d5;

  display: flex;
  justify-content: flex-end;
  align-items: center;

  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.basket__item {
  max-height: 40px;
  margin: 0 40px;
  cursor: pointer;
  transition: transform 1s;
}

.basket__item:hover {
  transform: scale(1.1);
}

.basket__button {
  background: transparent;
  border: none;
  cursor: default;
}

.basket__menu {
  max-width: 1200px;
  width: 100%;
  margin: 10px auto;

  position: absolute;
  z-index: 1;
  height: calc(100% - 80px);
  background: #992667;
  top: -100%;
  box-shadow: 0 6px 10px 10px rgba(0,0,0,0.6);
  transition: transform 1s;
}

.basket__menu_active {
  transform: translateY(calc(100% + 140px));
}

/* http://meyerweb.com/eric/tools/css/reset/
	v2.0-modified | 20110126
	License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
    vertical-align: baseline;
    box-sizing: border-box;
}

/* make sure to set some focus styles for accessibility */
:focus {
    outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
    -webkit-appearance: none;
    -moz-appearance: none;
}

input[type=search] {
    -webkit-appearance: none;
    -moz-appearance: none;
    box-sizing: content-box;
}

textarea {
    overflow: auto;
    vertical-align: top;
    resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */

audio,
canvas,
video {
    display: inline-block;
    *display: inline;
    *zoom: 1;
    max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */

audio:not([controls]) {
    display: none;
    height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */

[hidden] {
    display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */

html {
    font-size: 100%; /* 1 */
    -webkit-text-size-adjust: 100%; /* 2 */
    -ms-text-size-adjust: 100%; /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */

a:focus {
    outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */

a:active,
a:hover {
    outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */

img {
    border: 0; /* 1 */
    -ms-interpolation-mode: bicubic; /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */

figure {
    margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */

form {
    margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */

fieldset {
    border: 1px solid #c0c0c0;
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */

legend {
    border: 0; /* 1 */
    padding: 0;
    white-space: normal; /* 2 */
    *margin-left: -7px; /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */

button,
input,
select,
textarea {
    font-size: 100%; /* 1 */
    margin: 0; /* 2 */
    vertical-align: baseline; /* 3 */
    *vertical-align: middle; /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */

button,
input {
    line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */

button,
select {
    text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */

button,
html input[type="button"], /* 1 */
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button; /* 2 */
    cursor: pointer; /* 3 */
    *overflow: visible;  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */

button[disabled],
html input[disabled] {
    cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */

input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box; /* 1 */
    padding: 0; /* 2 */
    *height: 13px; /* 3 */
    *width: 13px; /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */

input[type="search"] {
    -webkit-appearance: textfield; /* 1 */ /* 2 */
    box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */

button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */

textarea {
    overflow: auto; /* 1 */
    vertical-align: top; /* 2 */
}

/**
 * Remove most spacing between table cells.
 */

table {
    border-collapse: collapse;
    border-spacing: 0;
}

